let changelook= ['所有人可见', '仅自己可见'];
import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar.vue";
import uploudImages from "../../components/common/uploud-images.vue";
import uniPopup from "../../components/uni-popup/uni-popup.vue";
export default {
	components:{
		uniNavBar,
		uploudImages,
		uniPopup
	},
	data() {
		return {
			isget:false,
			showpopup:true,
			yinsi:"所有人可见",
			text:"",
			imglist:[]
		};
	},
	onBackPress() {
		// 如果有值,也就是填写了文字或者是添加了照片
		if(!this.text && this.imglist.length<1){ return; }
		if(!this.isget){
			this.baocun();
			return true;
		}
	},
	methods:{
		// 保存为草稿
		baocun(){
			uni.showModal({
				content: '是否要保存为草稿？',
				cancelText: '不保存',
				confirmText: '保存',
				success: res => {
					if(res.confirm){
						console.log("保存")
					}else{
						console.log("不保存")
					}
					this.isget=true;
					uni.navigateBack({
						delta: 1
					});
				},
			});
		},
		// 返回
		back(){
			uni.navigateBack({ delta: 1 });
		},
		// 发布
		submit(){
			console.log("发布")
		},
		// 隐私
		changelook(){
			uni.showActionSheet({
				itemList:changelook,
				success: (res)=> {
					this.yinsi=changelook[res.tapIndex]
				}
			});
		},
		// 图片上传组件 更新后 给 父组件 传递的imglist[]
		uploud(arr){
			this.imglist=arr;
			console.log(this.imglist)
		},
		// 蒙版层点击关闭弹出层
		hidePopup(){
			this.showpopup=false;
		}
	}
}
